.mobileProductMenu {
}

.mobileProductMenu-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.mobileProductMenu-list--normal {
}

.expired .mobileProductMenu-list--normal {
  display: none;
}

.mobileProductMenu-list--expired {
  display: none;
}

.expired .mobileProductMenu-list--expired {
  display: block;
}

.mobileProductMenu-item {
  border-bottom: 1px solid rgba(207, 214, 230, 0.5);
}

.mobileProductMenu-link {
  padding: 16px 0;
  display: block;
  text-decoration: none;
  color: #313237;
  font-weight: 500;
}

.mobileProductMenu-link:focus {
