.expired .conditionsOfCompetition-section {
  display: none;
}

.conditionsOfCompetition {
  background-color: rgba(179, 242, 226, 0.43);
  overflow: hidden;
  padding: 70px 0;
  position: relative;
}

@media (max-width: 840px) {
  .conditionsOfCompetition {
    padding: 40px 0;
  }
}

.conditionsOfCompetition::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background-image: url("../assets/images/bg_4.svg"),
    url("../assets/images/bg_3.svg"), url("../assets/images/bg_5.svg");
  background-position: right bottom, left bottom, right top;
  background-size: 184px 192px, 767px 606px, 754px 125px;
  background-repeat: no-repeat;
  z-index: 1;
  opacity: 0.7;
}

.conditionsOfCompetition-content {
  position: relative;
  z-index: 2;
}

.conditionsOfCompetition-container {
  max-width: 1262px;
  margin: 0 auto;
  padding: 64px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

@media (max-width: 640px) {
  .conditionsOfCompetition-container {
    padding: 36px;
  }
}

.conditionsOfCompetition-contentInner {
  position: relative;
}

.conditionsOfCompetition-contentInner::after {
  content: "";
  position: absolute;
  left: -42px;
  bottom: -42px;
  top: 32px;
  right: 32px;
  background-color: #231f20;
  background-image: url("../assets/images/bg_7.svg");
  background-size: 404px 224px;
  background-repeat: no-repeat;
  background-position: bottom 8px left 8px;
  z-index: 3;
}

@media (max-width: 640px) {
  .conditionsOfCompetition-contentInner::after {
    background-size: 202px 112px;
    left: -26px;
    bottom: -26px;
  }
}

.conditionsOfCompetition-contentMain {
  position: relative;
  background-color: #fff;
  padding: 48px 32px;
  z-index: 4;
}

@media (max-width: 840px) {
  .conditionsOfCompetition-contentMain {
    padding: 24px 16px;
  }
}

.conditionsOfCompetition-contentVisual {
  position: absolute;
  left: -18px;
  bottom: 27px;
  top: -15px;
  right: -24px;
}

@media (max-width: 640px) {
  .conditionsOfCompetition-contentVisual {
    position: absolute;
    left: -12px;
    bottom: 22px;
    top: -12px;
    right: -12px;
  }
}

.conditionsOfCompetition-contentVisualInner {
  width: 100%;
  height: 100%;
  position: relative;
}

.conditionsOfCompetition-contentVisualInner::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: -150px;
  width: 237px;
  height: 161px;
  background-image: url("../assets//images/bg_8.svg");
  background-size: contain;
  background-repeat: no-repeat;
  z-index: 1;
  opacity: 0.7;
}

.conditionsOfCompetition-contentVisual::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  border: 4px solid #231f20;
  box-sizing: border-box;
  z-index: 3;
}

@media (max-width: 640px) {
  .conditionsOfCompetition-contentVisual::after {
    border-width: 3px;
  }
}

.conditionsOfCompetition-contentVisual::before {
  content: "";
  position: absolute;
  left: 50px;
  top: -25px;
  right: -25px;
  bottom: 16px;
  background-color: #b3f2e2;
  background-image: url("../assets/images/bg_6.svg");
  background-size: 404px 224px;
  background-repeat: no-repeat;
  background-position: top 16px right 16px;
  z-index: 2;
}

@media (max-width: 640px) {
  .conditionsOfCompetition-contentVisual::before {
    background-size: 202px 112px;
    left: 34px;
    top: -14px;
    right: -12px;
    bottom: 16px;
    background-position: top 8px right 8px;
  }
}
