.navigationDrawer {
  width: 100%;
  height: 100%;
  /* background-color: pink; */
  overflow: auto;
  box-sizing: border-box;
}

.navigationDrawer-header {
}

.navigationDrawer-button {
  display: block;
  text-decoration: none;
  padding: 16px 0;
  margin: 0;
  width: 100%;
  font-size: 16px;
  font-family: inherit;
  background-color: #fdcd54;
  color: #313237;
  border: none;
  outline: none;
  cursor: pointer;
  font-weight: 500;
  text-align: center;
}

.isKeyboardUsing .navigationDrawer-button:focus {
  box-shadow: inset 0 0 0 2px rgba(47, 124, 192, 0.4);
}

.navigationDrawer-body {
  padding: 24px 8px;
}
