.buttonIcon {
  display: inline-block;
  margin: 0;
  padding: 8px;
  border: 1px solid transparent;
  line-height: 1;
  background-color: transparent;
  color: inherit;
  outline: none;
  appearance: none;
  text-decoration: none;
  cursor: pointer;
  vertical-align: top;
}

@media (max-width: 1280px) {
  .buttonIcon {
    padding: 4px;
  }
}
