.section {
  padding: 24px 0;
  margin-bottom: 24px;
}

.section-noGap {
  padding: 0;
}

.section-noOuterGap {
  margin: 0;
}

.section:last-child {
  margin-bottom: 0;
}

.section-header {
  display: flex;
  margin-bottom: 16px;
  position: relative;
  padding-left: 24px;
}

.section--size-small .section-header {
  margin-left: 0;
  margin-bottom: 48px;
}

.section-headerTitle {
  font-weight: 500;
  font-size: 36px;
  max-width: 830px;
  /* margin-left: 24px; */
}

@media (max-width: 840px) {
  .section-headerTitle {
    font-size: 24px;
  }
}

.section-headerAction {
  margin-left: auto;
  margin-right: 24px;
}

@media (max-width: 840px) {
  .section-headerAction {
    display: none;
  }
}

.section-content {
  overflow: hidden;
}

.section--overflowVisible .section-content {
  overflow: visible;
}

.section-footer {
  display: none;
  padding-top: 16px;
}

@media (max-width: 840px) {
  .section-footer {
    display: block;
  }
}

.section-crossSerifs {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

