.mainForm {
}

.mainForm-row {
  margin-bottom: 24px;
  padding-top: 16px;
}

.mainForm-row:first-child {
  padding-top: 0;
}

.mainForm-row:last-child {
  margin-bottom: 0;
}

.mainForm-row--noLabelGap {
  padding-top: 0;
}

.mainForm-row--labelTop {
  padding-top: 32px;
}

.mainForm-row:last-child {
  margin-bottom: 0;
}

.mainForm-row--labelTop:last-child {
  margin-bottom: 0;
}

.mainForm-title {
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 48px;
}

.mainForm-smalTitle {
  text-align: left;
}

.mainForm-labeledRow {
  margin-bottom: 24px;
  padding-top: 40px;
}

.mainForm--smallGap {
  padding-top: 18px;
}

/* @media (max-width: 640px) {
  .mainForm-labeledRow {
    margin-bottom: 8px;
  }
} */

.mainForm-labeledRow:last-child {
  margin-bottom: 0;
}

.mainForm-actionRow {
  margin-bottom: 24px;
  padding-top: 24px;
  text-align: right;
}

@media (max-width: 640px) {
  .mainForm-actionRow {
    margin-bottom: 8px;
    padding-top: 16px;
  }
}

.mainForm-actionRow:last-child {
  margin-bottom: 0;
}

.mainForm-leftLabeledRow {
  margin-bottom: 24px;
  padding-top: 16px;
}

@media (max-width: 840px) {
  .mainForm-leftLabeledRow {
    margin-bottom: 24px;
    padding-top: 40px;
  }
}

@media (max-width: 640px) {
  .mainForm-leftLabeledRow {
    margin-bottom: 8px;
  }
}

.mainForm-part {
}

.mainForm-partLabel {
  text-align: center;
  font-weight: bold;
  margin: 24px 0 32px;
}

.mainForm-partContent {
}
