.header {
  padding: 16px 0;
  background-color: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(7px) hue-rotate(100deg);
  transition: 0.2s ease-out;
}

.page--navigationOpened .header {
  background-color: #fff;
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.header-navigation {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.header-hamburger {
  width: 24px;
  height: 24px;
  display: none;
}

@media (max-width: 1100px) {
  .header-hamburger {
    display: block;
  }
}
