.mainInput {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  max-width: 100%;
}

.mainInput--small {
}

.mainInput--focused {
}

.mainInput-field {
  appearance: none;
  outline: none;
  display: inline-block;
  width: 100%;
  background: none;
  background-color: #fff;
  padding: 16px 18px;
  line-height: 16px;
  font-size: 14px;
  height: 44px;
  border: 1px solid #dfe3ea;
  border-radius: 25px;
  color: #262931;
  box-sizing: border-box;
  margin: 0;
  font-family: inherit;
  cursor: default;
}

.mainInput-field[type='number']::-webkit-outer-spin-button,
.mainInput-field[type='number']::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

.mainInput-field[type='number'] {
  -moz-appearance: textfield;
}

.mainInput--withoutStroke .mainInput-field {
  border-color: transparent;
}

.mainInput-field:focus {
  border-color: #a1a6af;
}

.mainInput-field:disabled {
  cursor: default;
  background-color: #fafafa;
  color: #999999;
}

.mainInput--small .mainInput-field {
  padding: 11px 24px;
  border-radius: 20px;
  height: 40px;
}

.mainInput--nocontrols .mainInput-field::-webkit-clear-button,
/* .mainInput--nocontrols .mainInput-field::-webkit-calendar-picker-indicator, */
.mainInput--nocontrols .mainInput-field::-webkit-inner-spin-button {
  display: none;
}

.mainInput--focused .mainInput-field {
  border-color: #444952;
}

.mainInput--focused.mainInput--withoutStroke .mainInput-field {
  border-color: transparent;
}

.mainInput--withLeftGap .mainInput-field {
  padding-left: 50px;
}

.mainInput--small.mainInput--withLeftGap .mainInput-field {
  padding-left: 40px;
}

.mainInput--withRightGap .mainInput-field {
  padding-right: 50px;
}

.mainInput--small.mainInput--withRightGap .mainInput-field {
  padding-right: 40px;
}

.mainInput-button {
  padding: 4px;
  display: inline-block;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 8px;
  transform: translate(-50%, -50%);
}

.mainInput-icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: top;
}

.mainInput--small .mainInput-button {
  right: 4px;
}

.mainInput--small .mainInput-icon {
  width: 14px;
  height: 14px;
}

.mainInput-prepended,
.mainInput-appended {
  position: absolute;
  display: inline-block;
  width: 50px;
  height: 50px;
  top: 0;
}

.mainInput-prepended {
  left: 0;
}

.mainInput-appended {
  right: 0;
}

.mainInput--small .mainInput-prepended,
.mainInput--small .mainInput-appended {
  width: 40px;
  height: 40px;
}

.mainInput--error .mainInput-field {
  /* color: #e74a5a; */
  border-color: #e74a5a;
}

.mainInput--selectable .mainInput-field {
  cursor: text;
}
