.mainField {
  position: relative;
  cursor: default;
}

.mainField--notAbsoluteLabel {
}

.mainField-labelRow {
  position: absolute;
  display: flex;
  width: 100%;
  box-sizing: border-box;
  justify-content: space-between;
  left: 0;
  top: -32px;
  line-height: 1.4;
  justify-content: center;
}

.mainField--notAbsoluteLabel .mainField-labelRow {
  position: static;
  margin-bottom: 8px;
  top: 0;
}

.mainField-label {
  color: #262931;
}

.mainField-instance {
}

.mainField-meta {
  position: absolute;
  left: 0;
  top: 100%;
  margin-top: 4px;
  /* padding-left: 8px; */
  font-size: 14px;
  font-weight: 300;
}

.mainField-exampleList {
  height: 1.5em;
  height: 1.5;
  overflow: hidden;
  color: #797979;
}

.mainField-exampleItem {
  display: inline-block;
  vertical-align: top;
}

.mainField-wordItroduction {
  display: inline-block;
  padding-right: 12px;
  vertical-align: top;
}

.mainField-wordSeparator {
  display: inline-block;
  padding: 0 12px;
  vertical-align: top;
}

.mainField-error {
  color: #ff0000;
  font-size: 14px;
  font-weight: 300;
}
