.mainButton {
  display: inline-flex;
  appearance: none;
  outline: none;
  margin: 0;
  padding: 8px 32px;
  border: 1px solid #969696;
  text-decoration: none;
  font-family: inherit;
  background-color: transparent;
  border-radius: 30px;
  cursor: pointer;
  transition: 0.2s ease-out;
  text-align: center;
  box-sizing: border-box;
  align-items: center;
  min-width: 180px;
  text-align: center;
  justify-content: center;
}

.mainButton--primary {
  background-color: #111;
  border: 1px solid transparent;
}

.mainButton-iconLeft {
  margin-right: 8px;
}

.mainButton--expanded {
  width: 100%;
}

.mainButton:not(:disabled):hover {
  background-color: #bababa;
  border: 1px solid #bababa;
}

.mainButton:disabled {
  /* border: 1px solid #bababa; */
  background-color: #bababa;
  cursor: default;
}

.mainButton-text {
  font-size: 18px;
  /* text-transform: uppercase; */
  color: #231f20;
  padding: 4px;
  display: inline-block;
  text-align: center;
}

.mainButton--primary .mainButton-text {
  color: #fff;
}

.mainButton:disabled .mainButton-text {
  color: #bababa;
}

.mainButton:disabled .mainButton-text {
  color: #fff;
}

.mainButton:not(:disabled):hover .mainButton-text {
  color: #fff;
}

.mainButton--transparent {
  background-color: transparent;
}

.mainButton-holder {
  margin: -4px;
  display: flex;
  align-items: center;
}
.mainButton-icon {
  padding: 4px;
  display: inline-block;
}
